import "antd/dist/antd.css";
import { Typography, Divider } from "antd";
import "./Styles.css";
import { Table, Tag, Space } from "antd";

const { Title, Paragraph, Text, Link } = Typography;
export default () => {
  const dataSource = [
    {
      key: "1",
      name: "HACK N PITCH",
      organizer: "JU ECELL",
      submission: "",
      tags: ["YOLO v3", "Here Maps API", "Tkinter"]
    },
    {
      key: "2",
      name: "Hach Inout 7.0",
      organizer: "Devfolio",
      tags: ["Django", "Django-rest-framework", "ReactJS"]
    },
    {
      key: "3",
      name: "Flipr Hackathon 8.0",
      organizer: "Flipr Innovation Labs",
      tags: ["Spring Boot", "ReactJS", "MongoDB", "MySQL", "Heroku"]
    }
  ];
  const columns = [
    {
      title: "Name of Hackathon",
      dataIndex: "name",
      key: "name"
    },
    {
      title: "Organizer",
      dataIndex: "organizer",
      key: "organizer"
    },
    {
      title: "Official Submission",
      dataIndex: "submission",
      key: "submission"
    },
    {
      title: "Technologies Used",
      key: "tags",
      dataIndex: "tags",
      render: (tags) => (
        <>
          {tags.map((tag) => {
            let color = tag.length > 7 ? "geekblue" : "green";
            if (tag === "loser") {
              color = "volcano";
            }
            return (
              <Tag color={color} key={tag}>
                {tag}
              </Tag>
            );
          })}
        </>
      )
    }
  ];

  return (
    <div className="Hacks">
      <Title level={2} className="Title">
        Hackathons
      </Title>
      <Table dataSource={dataSource} columns={columns} />;
    </div>
  );
};
