import { Typography } from "antd";

export default function name() {
  return (
    <div className="Name">
      <Typography>
        <h1
          style={{
            padding: 5,
            fontSize: 42,
            fill: "white",
            textAlign: "center",
            stroke: "white",
            strokeWidth: 2
          }}
        >
          Saikat Chakraborty
        </h1>
      </Typography>
    </div>
  );
}
